* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-white: #ffffff;
  --color-light: #ffffff3f;
  --color-black: #000;

  --transition: all 400ms ease;

  --container-width-lg: 96%;
  --container-width-md: 96%;
  --container-width-sm: 96%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

body {
  /*BLUE*/
  background-image: linear-gradient(
    45deg,
    hsl(222deg 88% 29%) 0%,
    hsl(217deg 89% 30%) 11%,
    hsl(212deg 90% 31%) 22%,
    hsl(207deg 91% 31%) 33%,
    hsl(202deg 93% 32%) 44%,
    hsl(197deg 93% 33%) 56%,
    hsl(192deg 94% 34%) 67%,
    hsl(187deg 95% 35%) 78%,
    hsl(182deg 97% 36%) 89%,
    hsl(177deg 98% 36%) 100%
  );
