  font-family: "Lato", sans-serif;
  color: var(--color-white);
}

/* ================== GENERAL STYLE ===================*/
.container {
  width: var(--container-width-lg);
  height: 100vh;
  margin: auto auto;
  position: relative;
}
.btn-custom {
  width: 3.25rem;
  height: 3.25rem;
  background: var(--color-light);
  border-radius: 50%;
  cursor: pointer;
  color: var(--color-white);
  padding-top: 3px;
  padding-left: 1px;
}

input {
  color: var(--color-white);
  background: var(--color-light);
  border-radius: 0.25rem;
  width: 5rem;
  padding: 0.75rem 1rem;
  margin: 0 1rem;
}
input:focus-visible {
  outline: none;
}
